function parseTimeToMs(string) {
    if (string.endsWith("ms")) {
        return parseFloat(time);
    } else if (string.endsWith("s")) {
        return parseFloat(string) * 1000;
    }

    return 0;
}

function toggleMenu() {

    const closedTransform = -(settingsMenuContainer.offsetWidth + closePaddingRight * 2);

    conversationsMenuContainer.style.transform = "translateX(0)";
    settingsMenuContainer.style.transform = `translateX(${closedTransform}px)`;

    if (chatMenu.classList.contains("settings-open")) {
        conversationsMenuContainer.style.transform = `translateX(${closedTransform}px)`;
        settingsMenuContainer.style.transform = `translateX(0)`;
    }

    const width = chatMenu.offsetWidth;
    let transform = width - (3.25 * rootFontSize);


    const delays = window.getComputedStyle(chatMenu).transitionDuration;
    const delay = delays.split(',').map(s => s = s.trim());

    for (let element of containers.slice(1)) {
        if (chatMenu.classList.contains("open")) {
            element.style.width = `${menuWidth - openPaddingRight * 2}px`;
        } else {
            element.style.width = `${menuWidth - openPaddingRight - closePaddingRight}px`;
        }
    }

    if (chatMenu.classList.contains("open") || chatMenu.classList.contains("hover")) {
        let translateX = menuWidth - window.innerWidth;
        if (translateX > 0) {
            translateX = 0;
        }

        toggleMenuBtn.querySelector(".tooltip p").textContent = "Close Menu";

        chatMenu.style.transform = `translateX(${translateX}px)`;
        // menuHead.style.transform = `translateX(${translateX}px)`;
        chatMenu.style.paddingLeft = `${Math.abs(translateX) + (0.5 * rootFontSize)}px`

    } else {
        chatMenu.style.transform = `translateX(${-transform}px)`;
        // menuHead.style.transform = `translateX(${transform}px)`;

        toggleMenuBtn.querySelector(".tooltip p").textContent = "Open Menu";
    }

    // console.log(parseTimeToMs(delay[0]))
}

toggleMenuBtn.addEventListener("click", () => {
    chatMenu.classList.toggle("open");

    if (!(chatMenu.classList.contains("conversations-open") ||
        chatMenu.classList.contains("settings-open"))) {
        chatMenu.classList.add("conversations-open");
    }

    toggleMenu();
});

