const hoverOpen = false;

document.addEventListener("mousemove", (event) => {
    if (!hoverOpen) return;
    const rect = chatMenu.getBoundingClientRect();

    const clientX = event.clientX;
    const clientY = event.clientY;

    const isInside = (
        clientX >= rect.left && clientX <= rect.right &&
        clientY >= rect.top && clientY <= rect.bottom
    );

    if (isInside &&
        !chatMenu.classList.contains("hover") &&
        !chatMenu.classList.contains("close-hover")) {

        chatMenu.classList.add("hover");
        toggleMenu();
    } else if (!isInside &&
        chatMenu.classList.contains("hover")) {

        chatMenu.classList.remove("hover");
        if (!chatMenu.classList.contains("open")) {
            toggleMenu();
        }
    }
});

toggleMenu();

/*-------------------- TAG INPUT + USER ID SELECTION --------------------*/
const tagInput = chatMenu.querySelector(".tag-input"),
    input = tagInput.querySelector("input"),
    tagsContainer = tagInput.querySelector(".tags"),
    addUserBtn = tagInput.querySelector("#add-user-button");

let tags = [];

function createTag(label) {
    // Create the tag element
    const div = document.createElement("div");
    div.className = "tag";

    const text = document.createElement('span');
    text.classList.add('tag-label');
    text.textContent = label;

    const close = document.createElement("span");
    close.className = "remove-tag";
    const icon = document.createElement("i");
    icon.className = "fa-solid fa-xmark";
    close.appendChild(icon);

    close.addEventListener("click", () => {
        tags = tags.filter(tag => tag !== label);
        updateTags();
    });

    div.appendChild(text);
    div.appendChild(close);
    return div;
}

function clearTags() {
    // Remove all the elements in the tags container
    while (tagsContainer.firstChild) {
        tagsContainer.firstChild.remove();
    }
}

function updateTags() {
    clearTags();
    for (let tag of tags) {
        // Create each tag and append it to the tag container
        const tagElement = createTag(tag);
        tagsContainer.appendChild(tagElement);
    }

    // Scroll to the bottom of the tag container where the new tag was added
    tagsContainer.scrollTop = tagsContainer.scrollHeight - tagsContainer.offsetHeight;
}

const keysPressed = {};

document.addEventListener("keydown", (e) => {
    let key = e.key.toLowerCase();
    // Adds the key to a list of keys being currently pressed
    keysPressed[key] = true;

    if (document.activeElement === input) {
        if (key === "enter" || key === ',') {
            e.preventDefault();

            addUserBtn.click();
        } else if (key === "backspace" && input.value === '') {
            if (keysPressed["control"]) {
                tags = [];
                updateTags();
            } else {
                tags.pop();
                updateTags();
            }
        }
    }

    if (document.activeElement === messageInput) {
        if (key === "enter") {
            e.preventDefault();

            if (e.ctrlKey) {
                const start = messageInput.selectionStart;
                const end = messageInput.selectionEnd;
                const value = messageInput.value;

                messageInput.value = value.slice(0, start) + "\n" + value.slice(end);

                messageInput.selectionStart = messageInput.selectionEnd = start + 1;

                updateTextareaSize(messageInput);
            } else {
                sendMessageBtn.click();
            }
        }
        if (!keysPressed["control"] && keysPressed["enter"]) {

        } else if (keysPressed["control"] && keysPressed["enter"]) {

        }
    }
});

document.addEventListener("keyup", (e) => {
    let key = e.key.toLowerCase();
    // Removes the key from a list of keys being currently pressed
    keysPressed[key] = false;
});

addUserBtn.addEventListener("click", () => {
    const text = input.value.trim().replace(/,/g, '');
    if (text !== '' && !tags.includes(text)) {
        tags.push(text);
        updateTags();
    }
    input.value = '';

    input.focus();
});


