function createConversationDropdown(toggleId) {
    const container = document.createElement("div");
    container.classList.add("dropdown-content");
    container.dataset.toggle = toggleId;

    const options = document.createElement("a");
    options.classlist = "dropdown-option";
    const optionsIcon = document.createElement("i");
    optionsIcon.className = "fa-regular fa-clipboard";

    const text = document.createElement("p");
    text.textContent = "Options";

    options.append(optionsIcon, text);
    container.appendChild(options);

    return container;
}

function createConversation(conversation, id) {

    // Create the automatic name for the conversation based on number of users
    let name = conversation.name;
    if (!conversation.users.includes("global-chat")) {
        let otherUsers = conversation.users.filter(elem => elem != userId);

        if (!conversation.name) {

            if (otherUsers.length < 2) {
                name = otherUsers[0];
            } else if (otherUsers.length == 2) {
                let a = otherUsers.sort();
                name = `${a[0]} and ${a[1]}`;
            } else {
                name = `${otherUsers.sort()[0]} and ${otherUsers.length - 1} others`;
            }

            // console.log(`Other users in conversation "${name}"`, otherUsers);
        }
    }

    // console.log("Creating conversation", conversation)
    // Create the conversation element
    const div = document.createElement("div");
    div.className = "conversation";
    if (!conversation.users.includes("global-chat")) {
        div.setAttribute("data-users", conversation.users.join(','));
    } else {
        div.setAttribute("data-users", "global-chat");
        div.classList.add("global-chat");
    }

    const label = document.createElement("span");
    label.textContent = name;
    label.className = "conversation-name";

    const options = document.createElement("button");
    options.className = "conversation-options-button";
    options.id = id;

    const dropdown = createConversationDropdown(id);

    const icon = document.createElement("i");
    icon.className = "fa-solid fa-ellipsis";
    options.appendChild(icon);

    div.appendChild(label);
    div.appendChild(options);

    return { conversation: div, dropdown: dropdown };
}

function clearConversations() {
    while (conversationsContainer.firstChild) {
        // Remove all the conversation elements
        conversationsContainer.firstChild.remove();
    }
}

function updateConversations() {
    clearConversations();

    for (let i = 0; i < currentConversations.length; i++) {
        const id = `conversation-option${i}`;
        // create each conversation and append it to its container
        let conversationElement = createConversation(currentConversations[i], id);
        conversationElement.conversation.addEventListener("click", () => {
            setSelectedConversation(conversationElement.conversation)
        });

        conversationsContainer.appendChild(conversationElement.conversation);
        if (conversationElement.dropdown) {
            document.body.appendChild(conversationElement.dropdown);
        }
    }

    updateDropdowns();
}

function setSelectedConversation(element) {
    if (arraysAreEqual(conversationRecipientIds, element.dataset.users.split(','))) {
        return;
    }

    conversationRecipientIds = element.dataset.users.split(',');
    for (let conversation of conversationsContainer.children) {
        conversation.classList.remove("selected");
    }
    element.classList.add("selected");

    loadMessages(messages);
}

function addConversation(users) {
    // Remove any tags that do not have a user input
    let filteredUsers = users.filter(user => user.trim() !== '');
    filteredUsers.push(userId);
    filteredUsers = filteredUsers.sort();

    // console.log(filteredUsers);

    let alreadyExists = false;

    for (let i = 0; i < currentConversations.length; i++) {
        // Check if the conversation the user wants to make already exists
        const a = [...currentConversations[i].users].sort();
        const b = [...filteredUsers].sort();

        if (arraysAreEqual(a, b)) {
            alreadyExists = true;
            break;
        }
    }

    if (alreadyExists) {
        console.log("Conversation Already Exists");
        return;
    }

    if (filteredUsers.length === 1 && filteredUsers[0] === userId) {
        console.log("Cannot create a conversation with yourself");
    }

    sendToServer("create_conversation", { authToken: authToken, users: filteredUsers });
}

createConversationBtn.addEventListener("click", () => {
    if (tags.length > 0) {
        if (tags.length === 1 && tags[0] === userId) {
            console.log("Cannot create a conversation with yourself");
            return;
        }

        addConversation(tags);
    } else {
        console.log("Add users before creating a conversation");
    }
});

updateConversations();

